export const rules = {
  text: (value) => {
    if (!value) {
      return ',   ';
    } else {
      const nameRegEx = /[A-Za-z---]/;
      const result = nameRegEx.test(value);
      if (!result) {
        return '  ';
      }
    }
    return true;
  },

  checkbox: (value) => {
    if (value !== 'on') {
      return ',     ';
    }
    return true;
  },

  email: (value) => {
    if (!value) {
      return ',   ';
    } else {
      const mailRegEx = /[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/;
      const result = mailRegEx.test(value);
      if (!result) {
        return '    ';
      }
    }
    return true;
  },

  tel: (value) => {
    if (!value) {
      return ',   ';
    } else {
      const MIN_LENGTH = 17;
      if (value.length < MIN_LENGTH) {
        return '   ';
      }
    }
    return true;
  },
};
